import React, {Fragment, useEffect, useState} from 'react';
import {Schedule} from "./Schedule";
import CircularProgress from '@mui/material/CircularProgress';
import {Box} from "@mui/material";
import {instance, requestStatus} from "../../constants";

export const ScheduleContainer = () => {
    const [homework, setHomework] = useState(null);
    const [schedule, setSchedule] = useState(null);
    const [status1, setStatus1] = useState(requestStatus.LOADING);
    const [status2, setStatus2] = useState(requestStatus.LOADING);

   useEffect(() => {
        getSchedule();
        getHomework();
    }, []);

    const getHomework = (callback) => {
        instance
            .get(`/homework`)
            .then(res => {
                setHomework(res?.data);
                setStatus1(requestStatus.READY);
            })
            .catch(() => setStatus1(requestStatus.ERROR))
        if (callback) callback();
    };
    const getSchedule = (callback) => {
        instance
            .get(`/homework/schedule`)
            .then(res => {
                setSchedule(Object.values(res?.data)[0]);
                setStatus2(requestStatus.READY);
            })
            .catch(() => setStatus2(requestStatus.ERROR))
        if (callback) callback();
    };
    const addHomework = (value, pairId, date, callback) => {
        instance.post(`/homework`, {value, pairId, date})
            .then(() => callback && callback())
            .catch(() => {
            })
    }
    const deleteHomework = (id, callback) => {
        instance.delete(`/homework`, {data: {id: id}})
            .then(() => callback && callback())
            .catch(() => {
            })
    }
    return (
        <Fragment>
            {(!homework || !schedule) && (status1 === requestStatus.LOADING || status2 === requestStatus.LOADING) ? (
                <Box sx={{display: 'flex', justifyContent: 'center',paddingTop:'40vh'}}>
                    <CircularProgress/>
                </Box>
            ) : null}
            {schedule && homework && status1 === requestStatus.READY && status2 === requestStatus.READY ? (
                <Schedule
                    schedule={schedule}
                    homework={homework}
                    addHomework={addHomework}
                    getHomework={getHomework}
                    deleteHomework={deleteHomework}
                />
            ) : null}
        </Fragment>
    )
}