import * as React from "react";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Link from "@mui/material/Link";
import TextField from "@mui/material/TextField";
import Autocomplete from "@mui/material/Autocomplete";
import Dialog from "@mui/material/Dialog";
import DialogTitle from "@mui/material/DialogTitle";
import DeleteOutlinedIcon from '@mui/icons-material/DeleteOutlined';
import {TabPanel} from './TabPanel'
import IconButton from "@mui/material/IconButton";
import {styled} from "@mui/material";
import Typography from "@mui/material/Typography";
import img1 from "../../img/2456073.png";
import {Fragment} from "react";

const FieldsContainer = styled(Box)({
    display: 'flex',
    flexDirection: 'column',
    '& > *': {
        paddingBottom: '16px!important'
    }
})

const ButtonsContainer = styled(Box)({
    display: 'flex',
    maxWidth: '100%',
    width: 400,
    justifyContent: 'space-between',
    '@media (max-width: 600px)': {
        flexDirection: 'column',
        '& *': {
            marginBottom: 8
        }
    }
})
const islink = (string) => {
    try {
        let url = new URL(string);
        return true
    } catch (_) {
        return false;
    }
}

export function Materials({materials, getMaterials, addMaterial, deleteMaterial}) {
    const [section, setSection] = React.useState(materials[0]?.section);
    const [addWindowOpened, setAddWindowOpened] = React.useState(false);
    const [title, setTitle] = React.useState("");
    const [group, setGroup] = React.useState("");
    const [materialValue, setMaterialValue] = React.useState("");

    const sortArrayByGroup = (x, y) => {
        if (x.section > y.section) {
            return 1;
        }
        if (x.section < y.section) {
            return -1;
        }
        return 0;
    };

    const sortArrayByTitle = (x, y) => {
        if (x.title > y.title) {
            return 1;
        }
        if (x.title < y.title) {
            return -1;
        }
        return 0;
    };

    const getUniqueMaterialGroups = () => {
        return [...new Set(materials.map((material) => material.section))];
    };

    const handleChange = (event, newValue) => {
        setSection(newValue);
    };

    const resetForm = () => {
        setTitle("");
        setGroup("");
        setMaterialValue("");
    };

    const handleClose = () => {
        setAddWindowOpened(false)
    }

    const handleSubmit = () => {
        addMaterial(title, materialValue, group, getMaterials);
        resetForm();
        handleClose();
    };

    const handleAddWindowOpen = () => {
        setAddWindowOpened(true);
        setGroup("");
        setTitle("");
        setMaterialValue("");
    };


    return (
        <Box maxWidth={800} sx={{width: {xs: '85vw', sm: 'auto'}}} marginX={'auto'}>
            <Box>
                <Box marginBottom={3}>
                    <Button variant="contained" sx={{width: '100%'}} onClick={handleAddWindowOpen}>
                        Добавить материал
                    </Button>
                </Box>
                <Dialog open={addWindowOpened}>
                    <Box marginX={3} marginY={1}>
                        <DialogTitle style={{textAlign: 'center'}}>Добавить материал</DialogTitle>
                        <FieldsContainer marginY={1}>
                            <Autocomplete
                                freeSolo
                                options={getUniqueMaterialGroups()}
                                renderInput={(params) => (
                                    <TextField
                                        {...params}
                                        value={group}
                                        label="Раздел материала"
                                        onChange={(e) => setGroup(e.target.value)}
                                        onSelect={(e) => setGroup(e.target.value)}
                                        required
                                        fullWidth
                                        type="text"
                                    />
                                )}
                            />
                            <TextField
                                value={title}
                                onChange={(e) => setTitle(e.target.value)}
                                required
                                fullWidth
                                type="text"
                                placeholder="Название материала"
                            />
                            <TextField
                                value={materialValue}
                                onChange={(e) => setMaterialValue(e.target.value)}
                                required
                                fullWidth
                                type="text"
                                placeholder="Материал"
                            />
                        </FieldsContainer>
                        <ButtonsContainer marginTop={2} marginBottom={1}>
                            <Button
                                type="reset"
                                variant="outlined"
                                color="primary"
                                onClick={resetForm}
                            >
                                Очистить
                            </Button>
                            <Button
                                variant="contained"
                                color="primary"
                                onClick={handleSubmit}
                            >
                                Добавить
                            </Button>
                            <Button
                                variant="outlined"
                                color="error"
                                onClick={handleClose}
                            >
                                Закрыть
                            </Button>
                        </ButtonsContainer>
                    </Box>
                </Dialog>
            </Box>

            <Box sx={{
                flexGrow: 1, display: {xs: 'block', sm: "flex"},
                boxShadow: '3px 3px 4px 2px rgba(194, 214, 255, .7)',
                bgcolor: 'background.paper',
                p: 2,
                borderRadius: '13px',
            }}>
                {materials.length === 0 ?
                    <Box sx={{width: '50%', margin: '0 auto'}}>
                        <Typography sx={{textAlign: 'center'}}>Нет материалов</Typography>
                        <img src={img1} style={{width: '100%'}} alt=""/>
                    </Box> :
                    <Fragment>
                        <Tabs
                            orientation="vertical"
                            variant="scrollable"
                            value={section}
                            onChange={handleChange}
                            sx={{
                                borderRight: 1, borderColor: "divider",
                                width: {xs: '100%', sm: '30%'},
                                maxHeight:{xs:'30vh',sm:'100%'}
                            }}
                        >
                            {getUniqueMaterialGroups()
                                .sort(sortArrayByGroup)
                                .map((group, id) => (
                                    <Tab key={id} label={group} value={group}/>
                                ))
                            }
                        </Tabs>
                        <Box sx={{
                            display: "flex",
                            flexDirection: "column",
                            width: {xs: '100%', sm: '70%'}
                        }}>
                            {materials.sort(sortArrayByTitle).map((material, id) => (
                                <TabPanel key={id} value={section} index={material.section}>
                                    <div style={{
                                        color: "black",
                                        fontSize: "20px",
                                        display: "flex",
                                        justifyContent: 'space-between'
                                    }}>
                                        {material.title}
                                        <IconButton
                                            color="primary"
                                            component="span"
                                            onClick={() => deleteMaterial(material.id, getMaterials)}
                                        >
                                            <DeleteOutlinedIcon/>
                                        </IconButton>
                                    </div>
                                    <Box sx={{
                                        color: "gray", fontSize: "16px", overflow: 'hidden',
                                        textOverflow: 'ellipsis',
                                        whiteSpace: 'nowrap'
                                    }}>
                                        {islink(material.value) ?
                                            <Link title={material.value} href={material.value}
                                                  target='_blank'>{material.value}</Link>
                                            : <Typography title={material.value}>{material.value}</Typography>}
                                    </Box>
                                </TabPanel>
                            ))}
                        </Box>
                    </Fragment>}
            </Box>
        </Box>
    );
}
