import {postLogin, user} from './base';
import {initSchedule, initHomework} from './schedule';
import {initChat} from './chat';
import {initQueue} from './queue';
import {initMaterial} from './material';

let homework = initHomework();
let schedule = initSchedule();
let chat = initChat().reverse();
let queue = initQueue();
let material = initMaterial();

const randomId = () =>  Math.round(Math.random() * 1000000).toString();

export const instance = {
    post: (url, params) => {
        return new Promise((resolve) => {
            switch (url) {
                case '/user/login':
                    resolve({data: postLogin});
                    break;
                case '/user/logout':
                    resolve({});
                    break;
                case '/homework':
                    homework.push({id: randomId(), ...params})
                    resolve({});
                    break;
                case '/chat':
                    chat.unshift({id: randomId(), userName: user.name, userId: user.id, ...params})
                    break;
                case '/queue':
                    queue.push({id: randomId(), ...params, students: []})
                    resolve({});
                    break;
                case '/queue/interact':
                    queue.forEach((item, index) => {
                        if (item.id === params.id) {
                            let newStudentsList = item.students;
                            if (newStudentsList.map(student => student.userId).includes(user.id)) {
                                newStudentsList = newStudentsList.filter(student => student.userId !== user.id);
                            } else {
                                newStudentsList.push({userId: user.id, userName: user.name})
                            }
                            queue[index].students = newStudentsList;
                        }
                    })
                    resolve({});
                    break;
                case '/material':
                    material.push({id: randomId(), ...params})
                    resolve({});
                    break;
            }
        })
    },
    delete: (url, params) => {
        const {data} = params;
        return new Promise((resolve) => {
            switch (url) {
                case '/homework':
                    homework = homework.filter(item => item.id !== data.id);
                    resolve({});
                    break;
                case '/queue':
                    queue = queue.filter(item => item.id !== data.id);
                    resolve({});
                    break;
                case '/material':
                    material = material.filter(item => item.id !== data.id);
                    resolve({});
                    break;
            }
        })
    },
    get: (url) => {
        return new Promise((resolve) => {
            switch (url) {
                case '/homework/schedule':
                    resolve({data: schedule});
                    break;
                case '/homework':
                    resolve({data: [...homework]});
                    break;
                case '/chat':
                    resolve({data: chat});
                    break;
                case '/queue':
                    resolve({data: [...queue]});
                    break;
                case '/material':
                    resolve({data: [...material]});
                    break;
            }
        })
    },
}