import * as React from 'react';
import {
    AppBar,
    Box,
    CssBaseline,
    Drawer,
    IconButton,
    ListItemIcon,
    Toolbar,
    Avatar,
    Menu,
    Link,
    MenuItem
} from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import Logout from "@mui/icons-material/Logout";
import DateRangeIcon from '@mui/icons-material/DateRange';
import ChatIcon from '@mui/icons-material/Chat';
import AutoAwesomeMotionIcon from '@mui/icons-material/AutoAwesomeMotion';
import SupervisorAccountIcon from '@mui/icons-material/SupervisorAccount';
import {ChatContainer} from '../Chat/ChatContainer';
import {MaterialsContainer} from '../Materials/MaterialsContainer';
import {QueuesContainer} from '../Queues/QueuesContainer';
import {ScheduleContainer} from '../Schedule/ScheduleContainer';
import Sidebar from "./Sidebar";
import {BrowserRouter as Router, Switch, Route, Redirect} from "react-router-dom";
import PersonIcon from '@mui/icons-material/Person';
import {Toast} from "../Login/Toast";
import {LoginContainer} from "../Login/LoginContainer";
import {instance} from "../../constants";

const drawerWidth = 240;

function ResponsiveDrawer(props) {
    const [userId, setUserId] = React.useState(sessionStorage.getItem('UserId'));
    const [openToast, setOpenToast] = React.useState(false);
    const [mess, setMess] = React.useState(null);
    const menu = [
        {name: 'Расписание', icon: DateRangeIcon, component: <ScheduleContainer/>, path: '/schedule'},
        {name: 'Чат', icon: ChatIcon, component: <ChatContainer userId={userId}/>, path: '/chat'},
        {
            name: 'Очереди',
            icon: SupervisorAccountIcon,
            component: <QueuesContainer userId={userId}/>,
            path: '/queues'
        },
        {name: 'Материалы', icon: AutoAwesomeMotionIcon, component: <MaterialsContainer/>, path: '/materials'}
    ]
    const handleLogout = () => {
        instance
            .post(`/user/logout`, {})
            .then(() => {
                setUserId(null);
                sessionStorage.removeItem('UserId')
            })
            .catch(() => {
            })
    }
    const {window} = props;
    const [mobileOpen, setMobileOpen] = React.useState(false);
    const handleDrawerToggle = () => {
        setMobileOpen(!mobileOpen);
    };
    const [anchorEl, setAnchorEl] = React.useState(null);
    const open = Boolean(anchorEl);
    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };
    const handleClose = () => {
        setAnchorEl(null);
    };
    const container = window !== undefined ? () => window().document.body : undefined;
    return (
        <Router>
            {userId ?
                <Route path="/login">
                    <Redirect to="/schedule"/>
                </Route>
                :
                <Route path="*">
                    <Redirect to="/login"/>
                </Route>
            }
            <Toast setOpen={setOpenToast} open={openToast} mess={mess}/>
            {!userId ?
                <LoginContainer changeUserId={setUserId} setOpen={setOpenToast} setMess={setMess}></LoginContainer>
                :
                <Box sx={{display: 'flex'}}>
                    <CssBaseline/>
                    <AppBar
                        position="fixed"
                        sx={{
                            width: {sm: `calc(100% - ${drawerWidth}px)`},
                            ml: {sm: `${drawerWidth}px`},
                        }}>
                        <Toolbar sx={{display: 'flex', justifyContent: 'space-between'}}>
                            <IconButton
                                color="inherit"
                                aria-label="open drawer"
                                edge="start"
                                onClick={handleDrawerToggle}
                                sx={{mr: 2, display: {sm: 'none'}}}>
                                <MenuIcon/>
                            </IconButton>
                            <div></div>
                            <Box sx={{
                                display: 'flex',
                                alignItems: 'center',
                                justifyContent: 'end',
                                textAlign: 'center'
                            }}>
                                <IconButton onClick={handleClick} size="small" sx={{ml: 2}}>
                                    <Avatar sx={{width: 32, height: 32}}>
                                        <PersonIcon/>
                                    </Avatar>
                                </IconButton>
                            </Box>
                            <Menu
                                anchorEl={anchorEl}
                                open={open}
                                onClose={handleClose}
                                onClick={handleClose}
                                PaperProps={{
                                    elevation: 0,
                                    sx: {
                                        overflow: 'visible',
                                        filter: 'drop-shadow(0px 2px 8px rgba(0,0,0,0.32))',
                                        mt: 1.5,
                                        '& .MuiAvatar-root': {
                                            width: 32,
                                            height: 32,
                                            ml: -0.5,
                                            mr: 1,
                                        },
                                        '&:before': {
                                            content: '""',
                                            display: 'block',
                                            position: 'absolute',
                                            top: 0,
                                            right: 14,
                                            width: 10,
                                            height: 10,
                                            bgcolor: 'background.paper',
                                            transform: 'translateY(-50%) rotate(45deg)',
                                            zIndex: 0,
                                        },
                                    },
                                }}
                                transformOrigin={{horizontal: 'right', vertical: 'top'}}
                                anchorOrigin={{horizontal: 'right', vertical: 'bottom'}}>
                                <Link style={{textDecoration: 'none', color: '#2d2d2d'}}>
                                    <MenuItem onClick={handleLogout}>
                                        <ListItemIcon>
                                            <Logout fontSize="small"/>
                                        </ListItemIcon>
                                        Выйти
                                    </MenuItem>
                                </Link>
                            </Menu>
                        </Toolbar>
                    </AppBar>
                    <Box
                        component="nav"
                        sx={{width: {sm: drawerWidth}, flexShrink: {sm: 0}}}
                        aria-label="mailbox folders">
                        <Drawer
                            container={container}
                            variant="temporary"
                            open={mobileOpen}
                            onClose={handleDrawerToggle}
                            ModalProps={{
                                keepMounted: true,
                            }}
                            sx={{
                                display: {xs: 'block', sm: 'none'},
                                '& .MuiDrawer-paper': {boxSizing: 'border-box', width: drawerWidth},
                            }}>
                            <Sidebar menu={menu}/>
                        </Drawer>
                        <Drawer
                            open variant="permanent"
                            sx={{
                                display: {xs: 'none', sm: 'block'},
                                '& .MuiDrawer-paper': {boxSizing: 'border-box', width: drawerWidth},
                            }}>
                            <Sidebar menu={menu}/>
                        </Drawer>
                    </Box>
                    <Box
                        component="main"
                        sx={{flexGrow: 1, p: 3, width: {sm: `calc(100% - ${drawerWidth}px)`}}}>
                        <Toolbar/>
                        <Switch>
                            {menu.map((text, index) => (
                                <Route path={text.path} key={index}>
                                    {text.component}
                                </Route>
                            ))}
                        </Switch>
                    </Box>
                </Box>
            }
        </Router>
    );
}

export default ResponsiveDrawer;
