import React, {Fragment, useEffect, useState} from 'react';
import {Queues} from "./Queues";
import {instance,requestStatus} from "../../constants";
import {Box, CircularProgress} from "@mui/material";

export const QueuesContainer = ({userId}) => {
    const [data, setData] = useState(null);
    const [status, setStatus] = useState(requestStatus.LOADING);

    useEffect(() => getQueues(), []);

    const getQueues = (callback) => {
        instance
            .get(`/queue`)
            .then(res => {
                setData(res?.data);
                setStatus(requestStatus.READY);
            })
            .catch(() => setStatus(requestStatus.ERROR))
        if (callback) callback();
    };

    const addQueue = (title, callback) => {
        instance
            .post(`/queue`, {title})
            .then(() => callback && callback())
            .catch(() => {})
    };

    const deleteQueue = (id, callback) => {
        instance
            .delete(`/queue`, {data: {id}})
            .then(() => callback && callback())
            .catch(() => {})
    };

    const interactQueue = (id, callback) => {
        instance
            .post(`/queue/interact`, {id})
            .then(() => callback && callback())
            .catch(() => {})
    }


    return (
        <Fragment>
            {!data && status === requestStatus.LOADING ? (
                <Box marginX={3} marginY={4}>
                    <CircularProgress />
                </Box>
            ) : null}
            {data && status === requestStatus.READY ? (
                <Queues
                    userId={userId}
                    queues={data}
                    getQueues={getQueues}
                    addQueue={addQueue}
                    deleteQueue={deleteQueue}
                    enterQueue={interactQueue}
                    quitQueue={interactQueue}
                />
            ) : null}
        </Fragment>
    )
}