import React from "react";
import {useState} from "react";
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Switch from '@mui/material/Switch';
import Box from '@mui/material/Box';
import SendIcon from '@mui/icons-material/Send';
import {FormControlLabel} from "@mui/material";

export const InputArea = ({active, setActive, text, setText, addMessage, getMessages}) => {
    const [important, setImportant] = useState(false)

    const handleClick = () => {
        if (text) {
            setText('');
            addMessage(text, important, getMessages);
        }
    }

    return (
        <Box>
            {active ? (
                <Box>
                    <Box style={{display: 'flex'}}>
                        <TextField
                            placeholder="Введите сообщение"
                            multiline
                            variant="outlined"
                            value={text}
                            onChange={(e) => setText(e.target.value)}
                            label="Сообщение"
                            size={'small'}
                            fullWidth
                        />
                        <Box width={240} paddingLeft={1}>
                            <Button
                                onClick={handleClick}
                                variant="contained"
                                endIcon={<SendIcon/>}
                                fullWidth
                            >
                                Отправить
                            </Button>
                        </Box>
                    </Box>
                    <FormControlLabel control={<Switch onChange={() => setImportant(!important)} value={important}/>} label="Важное сообщение" />
                </Box>
            ) : (
                <Stack spacing={2} direction="row">
                    <Button onClick={() => setActive(!active)} variant="contained" fullWidth>
                        Открыть чат
                    </Button>
                </Stack>
            )}
        </Box>
    )
}


