import {React, useState} from 'react'
import {TextField, Button, Stack} from '@mui/material';
import AddIcon from '@mui/icons-material/Add';

const CreateQueue = ({getQueues, addQueue}) => {
    const [title, setTitle] = useState("");
    const itemEvent = (event) => {
        setTitle(event.target.value)
    };
    return (
        <Stack sx={{marginLeft: '16px', marginRight: '16px'}} spacing={2} direction="row">
            <TextField sx={{width: '100%'}} id="outlined-basic" label="Очередь" variant="outlined"
                       value={title}
                       onChange={itemEvent}/>
            <Button sx={{width: '45%', display: {xs: 'none', md: 'block'}}} variant="outlined" onClick={() => {
                addQueue(title, getQueues)
            }}>Добавить очередь </Button>
            <Button sx={{width: '30%', alignItems: 'center', display: {xs: 'flex', md: 'none'}}} variant="outlined"
                    onClick={() => {
                        addQueue(title, getQueues)
                    }}>
                <AddIcon/>
            </Button>
        </Stack>
    )
}


export default CreateQueue