import React, {useState} from 'react';
import {Box, Button, TextField, styled, Typography} from "@mui/material";
import img1 from '../../img/5293.png'
import {instance} from "../../constants";

const FirstContainer = styled(Box)({
    position: 'absolute', left: '0', top: '0',
    width: '100%',
    height: '100%',
    background: 'rgba(25, 118, 210, 0.05)'
})
const ContentContainer = styled(Box)({
    position: 'absolute', left: '50%', top: '50%',
    transform: 'translate(-50%, -50%)',
    background: 'white',
    display: 'flex',
    boxShadow: '0 4px 4px rgba(0, 0, 0, .2)',
    borderRadius: '8px'
})
const ImgContainer = styled(Box)({
    padding: '1rem',
    background: 'rgba(25, 118, 210, 0.4)',
    alignItems: 'center',
    borderBottomLeftRadius: '8px',
    borderTopLeftRadius: '8px',
    width: 320
})
const FormContainer = styled('form')({
    padding: '1rem',
    marginTop: 25,
    marginBottom: 25,
    display: 'flex',
    flexDirection: 'column',
    '& > *:not(:last-child)': {
        marginBottom: 16
    },
    '& > *:last-child': {
        marginTop: 16
    }
})

export const LoginContainer = ({changeUserId, setOpen, setMess}) => {
    const [login, setLogin] = useState('');
    const [pass, setPass] = useState('');
    const onFormSubmit = e => {
        e.preventDefault();
        instance
            .post(`/user/login`, {login, pass})
            .then((res) => {
                setMess('success')
                setOpen(true)
                changeUserId(res.data.id)
                sessionStorage.setItem('UserId', res.data.id)
            }).catch(e => {
                setMess('error')
                setOpen(true)
                console.error(e)
            }
        )
    }
    return (
        <FirstContainer>
            <ContentContainer sx={{width: {xs: '90%', sm: '70%', md: 'auto'}}}>
                <ImgContainer sx={{display: {xs: 'none', md: 'flex'}}}>
                    <img src={img1} style={{width: '100%'}} alt=""/>
                </ImgContainer>
                <FormContainer onSubmit={onFormSubmit} sx={{width: {xs: '100%', md: 320}}}>
                    <Typography variant={'h6'} style={{color: 'rgba(0,0,0,0.75)', fontWeight: 'bold'}}>
                        SuperApp for Students
                    </Typography>
                    <TextField
                        label="Логин"
                        variant="outlined"
                        size="small"
                        value={login}
                        onChange={(e) => setLogin(e.target.value)}/>
                    <TextField
                        label="Пароль"
                        variant="outlined"
                        type="password"
                        size="small"
                        value={pass}
                        onChange={(e) => setPass(e.target.value)}/>
                    <Button variant={'contained'} type='submit'>
                        Войти
                    </Button>
                </FormContainer>
            </ContentContainer>
        </FirstContainer>
    );
}