import * as React from 'react';
import './styleForTable.css'
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import PlaceIcon from "@mui/icons-material/Place";

export default function ForTable({lesson, colorMap}) {
    if (Object.keys(lesson).length !== 0) {
        return (
            <Box sx={{
                '& p': {
                    m: 0, fontSize: "small",
                    '@media (min-width: 992px)': {
                        textOverflow: 'ellipsis',
                        overflow: 'hidden',
                        whiteSpace: 'nowrap'
                    }
                },
                px: 1,
                '@media (min-width: 992px)': {
                    textOverflow: 'ellipsis',
                    overflow: 'hidden',
                    whiteSpace: 'nowrap'
                },
                borderLeft: '4px solid ' + colorMap[lesson.subject]
            }}>
                <Typography sx={{fontWeight: "bold"}} title={lesson.subject}>{lesson.subject}</Typography>
                <Box sx={{display: "flex"}}>
                    <PlaceIcon color="action" fontSize='small'/>
                    <Box style={{
                        textOverflow: 'ellipsis',
                        overflow: 'hidden',
                        whiteSpace: 'nowrap'
                    }}>
                        <Typography
                            title={lesson.room ? 'ауд.' + lesson.room : ''}>{lesson.room ? 'ауд.' + lesson.room : ''}
                        </Typography>
                        <Typography title={lesson.address}>{lesson.address}</Typography>
                    </Box>
                </Box>
                <Typography variant={"caption"} title={lesson.external}>{lesson.external}</Typography>
            </Box>
        )
    } else {
        return null;
    }
}
