import React from 'react';
import {Stack, Button, ListItem, Typography, Box, Divider} from '@mui/material';
import StudentsName from './StudentsName';
import AlertDialog from './AlertDialog'
import img1 from "../../img/2456073.png";

const Queue = ({userId, userName, queues, getQueues, deleteQueue, enterQueue, quitQueue}) => {
    return (
        <>
            <div>
                <Stack>
                    {queues.length === 0 ?
                        <Box sx={{width: '40%',pt:3, margin: '0 auto'}}>
                            <Typography sx={{textAlign: 'center'}}>Нет очередей</Typography>
                            <img src={img1} style={{width: '100%'}} alt="..."/>
                        </Box> :
                    queues.map((data, key) => (
                        <Box
                            sx={{
                                boxShadow: '3px 3px 4px 2px rgba(194, 214, 255, .7)',
                                bgcolor: 'background.paper',
                                m: 2,
                                p: 2,
                                borderRadius: '13px'
                            }}
                            key={key}>
                            <ListItem>
                                <Typography variant="body1" style={{width: "100%"}} gutterBottom>
                                    {key + 1}. {data.title}
                                </Typography>
                                <div style={{display: 'flex', justifyContent: "end", alignItems: 'end', width: '100%'}}>
                                    <AlertDialog deleteQueue={deleteQueue} getQueues={getQueues} q={data.id}/>
                                </div>
                            </ListItem>

                            <Divider/>
                            <StudentsName
                                currentQueues={data}
                                id={userId}
                                text={userName}
                                quitQueue={quitQueue}
                            />
                            {(data?.students?.find(item => item.userId === userId)) ? (
                                <Button
                                    sx={{width: '100%'}}
                                    variant="outlined"
                                    onClick={() => {
                                        quitQueue(data.id, getQueues)
                                    }}
                                >
                                    Выйти из очереди
                                </Button>
                            ) : (
                                <Button
                                    sx={{width: '100%'}}
                                    variant="outlined"
                                    onClick={() => {
                                        enterQueue(data.id, getQueues)
                                    }}
                                >
                                    Войти в очередь
                                </Button>
                            )}
                        </Box>
                    ))}
                </Stack>
            </div>

        </>

    )
}
export default Queue