import React, { useState} from 'react';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import img1 from '../../img/2456073.png'
import {Message} from "./Message";
import {InputArea} from './InputArea';


export const Chat = ({messages, getMessages, addMessage}) => {
    const [active, setActive] = useState(false)
    const [text, setText] = useState("")

    const messagesToDysplay = active ? messages : messages.filter(item => item.isImportant).slice(-5);

    return (
        <Box maxWidth={800} marginX={'auto'}>
            <InputArea
                messages={messages}
                text={text}
                setText={setText}
                active={active}
                setActive={setActive}
                getMessages={getMessages}
                addMessage={addMessage}
            />
            {!active ? (
                <Box marginTop={3} marginBottom={1}>
                    <Typography variant={'h5'}>Последние важные сообщения:</Typography>
                </Box>
            ) : null}
            <Box>
                {messagesToDysplay.length === 0 ?
                    <Box sx={{width: '50%',margin: '0 auto'}}>
                        <Typography sx={{textAlign:'center'}}>Нет сообщений</Typography>
                        <img src={img1} style={{width: '100%'}} alt=""/>
                    </Box> :
                    messagesToDysplay.map((item, id) => (
                        <Message
                            key={id}
                            id={item.id}
                            text={item.text}
                            userId={item.userId}
                            userName={item.userName}
                            isImportant={item.isImportant}
                        />
                    ))}
            </Box>
        </Box>
    );
}

