import React, {useState, useEffect, Fragment} from 'react';
import {Box, CircularProgress} from '@mui/material'
import {instance,requestStatus} from "../../constants";
import {Materials} from "./Materials";

export const MaterialsContainer = () => {
    const [data, setData] = useState(null);
    const [status, setStatus] = useState(requestStatus.LOADING);

    useEffect(() => getMaterials(), []);

    const getMaterials = (callback) => {
        instance
            .get(`/material`)
            .then(res => {
                setData(res?.data);
                setStatus(requestStatus.READY);
            })
            .catch(() => setStatus(requestStatus.ERROR))
        if (callback) callback();
    };

    const addMaterial = (title, value, section, callback) => {
        instance
            .post(`/material`, {title, value, section})
            .then(() => callback && callback())
            .catch(() => {})
    };

    const deleteMaterial = (id, callback) => {
        instance
            .delete(`/material`, {data: {id}})
            .then(() => callback && callback())
            .catch(() => {})
    };

    return (
        <Fragment>
            {!data && status === requestStatus.LOADING ? (
                <Box marginX={3} marginY={4}>
                    <CircularProgress />
                </Box>
            ) : null}
            {data && status === requestStatus.READY ? (
                <Materials
                    materials={data}
                    getMaterials={getMaterials}
                    addMaterial={addMaterial}
                    deleteMaterial={deleteMaterial}
                />
            ) : null}
        </Fragment>
    )
}