import React from 'react';
import Paper from '@mui/material/Paper';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import ForTable from "./ForTable";
import Box from "@mui/material/Box";
import PlaceIcon from '@mui/icons-material/Place';
import PersonIcon from '@mui/icons-material/Person';
import {Button, ButtonGroup, FormControl, Modal, TextField} from "@mui/material";
import IconButton from "@mui/material/IconButton";
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import {registerLocale} from "react-datepicker";
import ru from 'date-fns/locale/ru';
import Typography from "@mui/material/Typography";
import CloseIcon from '@mui/icons-material/Close';
import img1 from "../../img/2456073.png";

registerLocale('ru', ru);
const getWeek = (date) => {
    var oneSep = new Date(date.getFullYear(), 8, 1);
    return Math.ceil((((date - oneSep) / 86400000) + oneSep.getDay() - 1) / 7);
}
const pairTimes = [['08:20', '09:50'], ['10:00', '11:30'], ['11:40', '13:10'], ['13:30', '15:00'], ['15:20', '16:50'],
    ['17:00', '18:30'], ['18:40', '20:10'], ['20:20', '21:50']];
const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    borderRadius: '2%',
    boxShadow: 24,
    p: 4,
    pb: 8,
    '& #btnmodal': {
        position: "absolute",
        top: 0,
        right: 0,
        padding: '1rem'
    },
    '@media (max-width: 400px)': {
        width: '100%',
    },
};
const stylenew = {
    padding: '0',
    cursor: 'pointer',
    '&:hover': {
        background: '#eceff1'
    },
    width: '80%',
    '@media (min-width: 992px)': {
        width: '15%',
    },
};
const btnstyle = {
    bgcolor: "#1976d2!important",
    color: "#fff"
};
const tablemy = {}
const palette = [
    '#1bbc9b', '#2dcc70', '#3598db', '#9b58b5',
    '#34495e', '#f1c40f', '#e77e23', '#e84c3d',
    '#ecf0f1', '#95a5a5', '#16a086', '#27ae61',
    '#2a80b9', '#8f44ad', '#2d3e50', '#f39c11',
    '#d25400', '#c1392b', '#bec3c7', '#7e8c8d',
];
const subjectsSet = new Set();
const colorMap = {}
export const Schedule = ({schedule, homework, addHomework, deleteHomework, getHomework}) => {
    schedule.forEach(weekday =>
        weekday.timetable.forEach(({subject}) =>
            subject && subjectsSet.add(subject)
        )
    )
    Array.from(subjectsSet).forEach((subject, id) => colorMap[subject] = palette[id]);
    const [open, setOpen] = React.useState(false);
    const [width, setWidth] = React.useState(window.innerWidth);
    window.onresize = function (event) {
        setWidth(window.innerWidth);
    };
    const handleOpen = () => setOpen(true);
    const handleClose = () => {
        setOpen(false);
        setValueHw('');
        setFirstValueHw(true)
        setLesson({})
    };
    const [startDate, setStartDate] = React.useState(new Date());
    const [date, setDate] = React.useState(new Date());
    const [lesson, setLesson] = React.useState({});
    const [week, setWeek] = React.useState(getWeek(date));
    const [hw, setHw] = React.useState({});
    const [valueHW, setValueHw] = React.useState('');
    const [firstvalueHW, setFirstValueHw] = React.useState(true);
    const [dateHW, setDateHW] = React.useState(null);
    const ExampleCustomInput = React.forwardRef(({value, onClick}, ref) => {
        var curr = new Date(date.getTime());
        var firstday = null;
        if (curr.getDay() === 0) {
            firstday = new Date(curr.getTime() - (6 * 24 * 60 * 60 * 1000));
        } else {
            firstday = new Date(curr.setDate(curr.getDate() - curr.getDay() + 1));
        }
        var lastday = new Date(firstday.getTime() + (6 * 24 * 60 * 60 * 1000));
        return (
            <Button color="inherit" onClick={onClick} ref={ref} className='stylebtn'>
                {add0(firstday.getDate())}.{add0(firstday.getMonth() + 1)} - {add0(lastday.getDate())}.{add0(lastday.getMonth() + 1)}
            </Button>
        )
    });
    const dates = (date) => {
        let datess = [];
        var curr = new Date(date.getTime());
        var firstday = null;
        if (curr.getDay() === 0) {
            firstday = new Date(curr.getTime() - (6 * 24 * 60 * 60 * 1000));
        } else {
            firstday = new Date(curr.setDate(curr.getDate() - curr.getDay() + 1));
        }
        datess.push(firstday);
        for (let i = 1; i < 7; i++) {
            datess.push(new Date(firstday.getTime() + (i * 24 * 60 * 60 * 1000)))
        }
        return datess;
    }
    const highlightWithRanges = [{
        "react-datepicker__day--highlighted-custom-1": dates(date),
    }];
    const DateMy = (date) => {
        setStartDate(date);
        setDate(date);
        setWeek(getWeek(date));
    }
    const changeDateMy = (day = 0) => {
        let newdate = new Date();
        if (day !== 0) {
            let curr2 = new Date(date.getTime());
            newdate = new Date(curr2.setDate(curr2.getDate() + (day)))
        }
        DateMy(newdate)
    }
    const searchlesson = (dayId, start) => {
        var day = schedule.filter(row => row.dayId === dayId);
        if (day.length > 0) {
            var lesson = day[0].timetable.filter(row => (row.start === start && ((row.external === "Четная неделя" && (week % 2 === 0)) || (row.external.split(', ').indexOf(week.toString()) !== -1))));
            if (lesson.length > 0) {
                return lesson[0];
            }
        }
        return {};
    }
    const isfreeday = (dayId) => {
        var day = schedule.filter(row => row.dayId === dayId);
        if (day.length > 0) {
            var lesson = day[0].timetable.filter(row => ((row.external === "Четная неделя" && (week % 2 === 0)) || (row.external.split(', ').indexOf(week.toString()) !== -1)));
            if (lesson.length > 0) {
                return true
            }
        }
        return false
    }
    const add0 = (number) => {
        return number > 9 ? number : '0' + number;
    }
    const dateformat = (day) => {
        if (day) {
            return add0(day.getDate()) + '.' + add0(day.getMonth() + 1) + '.' + day.getFullYear();
        }
        return null;
    }
    const searchHW = (day, id) => {
        let datemy = dateformat(day);
        let t = homework.filter(hw => (hw.pairId === id) && (hw.date === datemy))
        if (t.length > 0) {
            return t[0];
        }
        return {}
    }
    return (
        <Box>
            <Modal
                open={open}
                onClose={handleClose}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description">
                <Box sx={style}>
                    <IconButton aria-label="delete" id="btnmodal" onClick={() => {
                        handleClose()
                    }}>
                        <CloseIcon/>
                    </IconButton>
                    <FormControl component="fieldset">
                        <Typography variant={'h6'}>Пара</Typography>
                        <Typography color="primary">{lesson.start}-{lesson.finish}</Typography>
                        <Typography><b>{lesson.subject}</b></Typography>
                        <Box sx={{display: "flex"}}>
                            <PersonIcon color="action"/>
                            <Typography>{lesson.instructor}</Typography>
                        </Box>
                        <Box sx={{display: "flex"}}>
                            <PlaceIcon color="action"/>
                            <div>
                                <Typography>{lesson.room ? 'ауд.' + lesson.room : ''}</Typography>
                                <Typography>{lesson.address}</Typography>
                            </div>
                        </Box>
                        <Typography>{lesson.external}</Typography>
                        <Typography variant={'h6'} sx={{pt: 2}}>Домашнее задание</Typography>
                        <TextField sx={hw.value ? {display: "none"} : {}}
                                   error={valueHW === '' && !firstvalueHW ? true : false}
                                   helperText={valueHW === '' && !firstvalueHW ? "Введите зaдание" : false}
                                   id="standard-basic" label="Задание"
                                   variant="standard"
                                   value={valueHW}
                                   onChange={(event) => {
                                       setValueHw(event.target.value)
                                       setFirstValueHw(false)
                                   }}/>
                        <Typography>{hw.value}</Typography>
                    </FormControl>
                    <Box style={{position: "absolute", bottom: 0, right: 0, padding: '1rem'}}>
                        {Object.keys(hw).length === 0 ?
                            <Button color="primary" onClick={() => {
                                if (valueHW) {
                                    addHomework(valueHW, lesson.pairId, dateformat(dateHW), () => {
                                        getHomework();
                                        handleClose()
                                    });
                                } else {
                                    setValueHw('');
                                    setFirstValueHw(false)
                                }
                            }}> Сохранить дз
                            </Button> :
                            <Button color="error" onClick={() => {
                                deleteHomework(hw.id, () => {
                                    getHomework();
                                    handleClose()
                                });
                            }}> Удалить дз
                            </Button>
                        }
                    </Box>
                </Box>
            </Modal>
            <Box sx={{display: 'flex', alignItems: "center"}}>
                <Button onClick={() => {
                    changeDateMy()
                }} color="inherit">Сегодня</Button>
                <ButtonGroup color="inherit">
                    <IconButton onClick={() => {
                        changeDateMy(-7)
                    }}>
                        <ChevronLeftIcon/>
                    </IconButton>
                    <IconButton onClick={() => {
                        changeDateMy(7)
                    }}>
                        <ChevronRightIcon/>
                    </IconButton>
                </ButtonGroup>
                <DatePicker locale="ru" selected={startDate} onChange={DateMy}
                            customInput={<ExampleCustomInput/>}
                            highlightDates={highlightWithRanges}/>
            </Box>
            <Box component={Paper} style={width >= 992 ? {} : {display: "none"}}>
                <Box style={{minWidth: 650}} fontSize="small" sx={tablemy}>
                    <Box>
                        <Box sx={{
                            display: "flex",
                            textAlign: "center",
                            padding: '6px 0',
                            borderBottom: '2px solid rgb(224, 224, 224)',
                            alignItems: "center"
                        }}>
                            <Box sx={{width: '10%'}}>
                                <AccessTimeIcon></AccessTimeIcon>
                            </Box>
                            {['Пн', 'Вт', 'Ср', 'Чт', 'Пт', 'Сб'].map((row, index) => (
                                <Box sx={{width: '15%'}} key={index}>
                                    {dates(date)[index].getDate()},&#160;{row}
                                </Box>
                            ))}
                        </Box>
                    </Box>
                    <Box>
                        {pairTimes.map((row, index) => (
                            <Box
                                key={index}
                                sx={{
                                    display: "flex", width: '100%',
                                    borderBottom: '1px solid rgb(224, 224, 224)'
                                }}>
                                <Box style={{
                                    width: '10%',
                                    display: "flex",
                                    alignItems: "center",
                                    justifyContent: "center",
                                    textAlign: "center"
                                }}>
                                    <p>
                                        {row[0]}<br/><small style={{color: 'gray'}}>{row[1]}</small>
                                    </p>
                                </Box>
                                {[0, 1, 2, 3, 4, 5].map((day, index) => {
                                    let les = searchlesson(day, row[0]);
                                    let hwork = searchHW(dates(date)[day], les.pairId);
                                    return (
                                        <Box key={index}
                                             style={Object.keys(hwork).length !== 0 ?
                                                 {background: 'rgb(25, 118, 210,0.1)'} :
                                                 {}}
                                             sx={Object.keys(les).length !== 0 ? stylenew : {
                                                 width: '15%'
                                             }}>
                                            <div onClick={() => {
                                                setDateHW(dates(date)[day]);
                                                setLesson(les);
                                                setHw(hwork);
                                                handleOpen();
                                            }}>
                                                <ForTable lesson={les} colorMap={colorMap}/>
                                            </div>
                                        </Box>
                                    )
                                })}
                            </Box>
                        ))}
                    </Box>
                </Box>
            </Box>
            <Box style={width < 992 ? {} : {display: "none"}}>
                <Box sx={{display: "flex", justifyContent: "space-between"}}>
                    {['Пн', 'Вт', 'Ср', 'Чт', 'Пт', 'Сб'].map((row, index) => (
                        <div style={{padding: '0.5rem', textAlign: 'center'}} key={index}>
                            <Typography>{row}</Typography>
                            <Button sx={dates(date)[index].getTime() === date.getTime() ? btnstyle : {}}
                                    style={{minWidth: 33}} color="inherit" onClick={() => {
                                setStartDate(dates(date)[index]);
                                setDate(dates(date)[index]);
                            }}>{dates(date)[index].getDate()}
                            </Button>
                        </div>
                    ))}
                </Box>
                {!isfreeday(date.getDay() - 1)?
                    <Box sx={{width: '40%',pt:3, margin: '0 auto'}}>
                        <Typography sx={{textAlign: 'center'}}>Нет пар</Typography>
                        <img src={img1} style={{width: '100%'}} alt="..."/>
                    </Box>
                    :pairTimes.map((row, index) => {
                    let day = date.getDay() - 1;
                    let les = searchlesson(day, row[0]);
                    let hwork = searchHW(dates(date)[day], les.pairId);
                    return (
                        <div key={index} style={{display: 'flex', paddingBottom: '0.5rem'}}>
                            <div style={{padding: '0 0.25rem', textAlign: "center", width: '20%'}}>
                                <p style={Object.keys(les).length === 0 ? {display: "none"} : {}}>
                                    <b>{les.start}</b><br/><small style={{color: 'gray'}}>{les.finish}</small>
                                </p>
                            </div>
                            <Box style={Object.keys(hwork).length !== 0 ? {background: 'rgb(25, 118, 210,0.1)'} : {}}
                                 sx={Object.keys(les).length !== 0 ? stylenew : {}}>
                                <div onClick={() => {
                                    setHw(hwork);
                                    setDateHW(dates(date)[day]);
                                    setLesson(les);
                                    handleOpen();
                                }}>
                                    <ForTable lesson={les} colorMap={colorMap}/>
                                </div>
                            </Box>
                        </div>
                    )
                })}
            </Box>
        </Box>
    );
}