import React from 'react';
import {Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle} from '@mui/material'
import ClearIcon from '@mui/icons-material/Clear';

const AlertDialog = ({deleteQueue, getQueues, q}) => {
    const [open, setOpen] = React.useState(false);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };
    return (
        <div>
            <Button sx={{marginBottom: '10px', display: {xs: 'none', md: 'block'}}} variant="outlined" onClick={handleClickOpen}>
                Удалить очередь
            </Button>
            <Button sx={{marginBottom: '10px', alignItems: 'center', display: {xs: 'flex', md: 'none'}}}
                    variant="outlined" onClick={handleClickOpen}>
                <ClearIcon/>
            </Button>
            <Dialog
                open={open}
                // onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">
                    {"Удалить очередь?"}
                </DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        Вы действительно хотите удалить очередь? Помните о том, что намеренное удаление очереди ведёт к
                        ответственности.
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose}>Выйти</Button>
                    <Button
                        onClick={() => {
                            deleteQueue(q, getQueues);
                            handleClose()
                        }}
                    >
                        Удалить
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    );
}

export default AlertDialog