import React from "react"
import { Stack, ListItem, Typography } from '@mui/material';

const StudentsName = ({ currentQueues }) => (
    <div>
        {currentQueues.students.map(({ userId, userName }, id) => (
            <div key={id}>
                <Stack spacing={2} direction="row">
                    <ListItem>
                        <Typography variant="body1" gutterBottom> {id + 1}. {userName} </Typography>
                    </ListItem>
                </Stack>
            </div>
        ))}
    </div>
)


export default StudentsName