import React from 'react';
import CreateQueue from './CreateQueue'
import Queue from './Queue'
import { Typography,Box } from '@mui/material'

export const Queues = ({ userId, queues, getQueues, addQueue, open, setOpen, deleteQueue, enterQueue, quitQueue }) => {
    return (
        <Box maxWidth={800} marginX={'auto'}>
            <Typography sx={{ fontSize: {xs:'15pt',md:'20pt'}, textAlign: 'center', fontWeight: '600' }} variant="body1" gutterBottom>
                Создать очередь или войти в существующую
            </Typography>
            <CreateQueue addQueue={addQueue} getQueues={getQueues} />
            <Queue
                queues={queues}
                userId={userId}
                getQueues={getQueues}
                open={open}
                setOpen={setOpen}
                quitQueue={quitQueue}
                deleteQueue={deleteQueue}
                enterQueue={enterQueue}
            />
        </Box>
    )
}