import React from "react";
import {Alert, Fade, Snackbar} from "@mui/material";

export const Toast = ({open, setOpen, mess}) => {
    const handleClose = () => {
        setOpen(false)
    };
    return (
        <Snackbar
            anchorOrigin={{vertical: 'bottom', horizontal: 'right'}}
            open={open}
            onClose={handleClose}
            TransitionComponent={Fade}
            autoHideDuration={4000}
            key={1}>
            <Alert onClose={handleClose} severity={mess} sx={{width: '100%'}}>
                {mess === 'success' ? 'Вы успешно вошли' : 'Ошибка авторизации'}
            </Alert>
        </Snackbar>
    )
}